import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';
import Markdown from 'preact-markdown';

import mu from '../../lib/musher-util';

import LoginForm from '../../components/login/loginForm';
import LoginRegister from '../../components/register';
import LoginForgot from '../../components/forgotten';

@observer
class GrantAccess extends Component {
    constructor(props) {
        super(props);
        this.state = {
            returnUrl: '',
            id: 0,
        };
    }

    loadApplication = () => {
        const { id } = this.state;
        const { applicationStore } = this.props.stores;
        applicationStore.load(id);
    }

    changeLanguage = (e) => {
        const lang = e.target.value;
		const { appState } = this.props.stores;
        appState.setLanguage(lang);
    }

    grantAccess = async () => {
        const { userStore } = this.props.stores;
        const { user } = userStore;
        const { returnUrl, id, team } = this.state;
        const grantObject = {
            grantedDate: new Date(),
            returnUrl,
            team,
            appId: id,
        };
        const existingApp = userStore.findApplicationById(id);
        if (!existingApp) {
            await userStore.saveField(user.id, 'applications', grantObject);
        }
        this.setState({
            accessGranted: 1,
        });
    }

    denyAccess = () => {
        route(`/`, true);
    }

    saveInputParams = () => {
        let { returnUrl, id } = this.props;
        if (returnUrl) {
            util.set('returnUrl', returnUrl);
            util.set('id', id);
        } else {
            returnUrl = util.get('returnUrl');
            id = util.get('id');
        }
        this.setState({
            returnUrl,
            id,
        });
    }

    componentWillMount() {
        this.saveInputParams();
    }

    componentDidMount() {
        this.loadApplication();
    }

    render() {
        const { appState, userStore, applicationStore } = this.props.stores;
        const { subsection } = this.props;
        const { returnUrl, id, accessGranted } = this.state;

        const { user } = userStore;
        const { language = 'en' } = appState;
        const { application } = applicationStore;

        if (!application) {
            return (
                <div class='container-fluid'>
                    <div class='row'>
                        <div class='col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>
                            <h1 class='mt-5'><i class='fas fa-paw' /> The Musher</h1>
                            <h5 class='mt-1 mb-5 font-weight-lighter'><Text id='grant-access.title'>Grant access</Text></h5>
                            <div class='alert alert-danger' role='alert'>
                                <h5>Error</h5>
                                Application not found!
                            </div>
                        </div>
                    </div>
                </div>
            );
        }

        const { title, body, email, images } = application;
        return (
            <div class='container-fluid'>
                <div class='row'>
                    <div class='col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>
                        <h1 class='mt-5'><i class='fas fa-paw' /> The Musher</h1>
                        <h5 class='mt-1 mb-5 font-weight-lighter'><Text id='grant-access.title'>Grant access</Text></h5>
                        <h5>{title}</h5>
                        <p>
                            <Markdown markdown={mu.replaceImages(body, images, language)} markdownOpts={mu.getMarkdownOptions()} />
                        </p>
                    </div>
                </div>
                <div class='row'>
                    {user.email ? <>
                            <div class='col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4'>

                            <div class='my-4'>
                                <h5 class='font-weight-light'>
                                    <Text id='grant-access.data-shared'>Data beeing shared</Text>:
                                </h5>
                                <p>
                                    <i class='fas fa-user' />: <Text id='grant-access.fields-shared-person'>Image, Name, email, cellphone, address</Text>
                                </p>
                                <p>
                                    <i class='fas fa-dog' />: <Text id='grant-access.fields-shared-dog'>Image, Name, chipId, gender, birth, vaccines</Text>
                                </p>
                                <p>
                                    <i class='fas fa-users' />: <Text id='grant-access.fields-shared-team'>Name, description, images</Text>
                                </p>
                            </div>
                            
                            {accessGranted ? <>
                                <a href={`${returnUrl}?userId=${user.uuidv4}`} native>Go back to application</a>
                            </> : <>                                
                                <button
                                    class='btn btn-success'
                                    onClick={this.grantAccess}
                                >
                                    <Text id='grant-access.grant'>Grant access</Text>
                                </button>

                                <button
                                    class='btn btn-sm btn-link text-muted float-right'
                                    onClick={this.denyAccess}
                                >
                                    <Text id='grant-access.deny'>Deny access</Text>
                                </button>
                            </>}
                        </div>
                    </> : <>
                        {!subsection && <LoginForm stores={this.props.stores}
                            showLogo={false}
                            routeLoginOk={`/grant-access`}
                            routeLoginNew={`/grant-access/newUser`}
                            routeLoginForgot={`/grant-access/forgotPassword`}
                        />}

                        {subsection === 'newUser' && <LoginRegister stores={this.props.stores}
                            showLogo={false}
                            routeLogin={`/grant-access`}
                            routeLoginForgot={`/grant-access/forgotPassword`}
                            skipPubSub={true}
                        />}

                        {subsection === 'forgotPassword' && <LoginForgot stores={this.props.stores}
                            showLogo={false}
                            routeLogin={`/grant-access`}
                            routeLoginNew={`/grant-access/newUser`}
                        />}
                    </>}

                </div>
                <div class='col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4'>
                    <div class='form-row align-items-center'>
							<div class='col-auto my-1 mx-auto' style='width: 80px;'>
								<select class='custom-select'
									style='background-color: inherit; border: none;'
									onInput={this.changeLanguage}
								>
									<option value='no' selected={language === 'no'}>🇳🇴 NO</option>
									<option value='en' selected={language === 'en'}>🇬🇧 EN</option>
                                    <option value='es' selected={language === 'es'}>🇪🇸 ES</option>
                                    <option value='de' selected={language === 'de'}>🇩🇪 DE</option>
								</select>
							</div>
						</div>
                </div>
            </div>
        );
    }
}

export default GrantAccess;
